@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital@0;1&family=Raleway:ital,wght@0,400;0,500;0,700;1,400&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.min.css);
@import url(https://pro.fontawesome.com/releases/v5.10.0/css/duotone.css);
* {
  --main-color: #590d22;
  --main-light-color: #800f2f;
  --hl-color: #febe27;
  --text-color: #fff;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Raleway", sans-serif;
  color: var(--text-color);
}

/**********************HEADER***********************/

.header {
  width: 100%;
  height: 70px;
  background-color: var(--main-color);
}

.header-nav {
  display: flex;
  height: 100%;
  justify-content: space-evenly;
  align-items: center;
  color: var(--hl-color);
}

.header-nav__link {
  color: var(--text-color);
}

.header-nav__link,
.header-nav__link:visited,
.header-nav__link:hover,
.header-nav__link:active {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: relative;
  transition: 0.5s color ease;
  text-decoration: none;
  text-transform: uppercase;
}

.header-nav__link:hover {
  color: var(--hl-color);
  font-weight: bold;
}

.header-nav__link:after {
  content: "";
  transition: 0.5s all ease;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
  bottom: -3px;
  height: 2px;
  width: 0;
  background: var(--hl-color);
  left: 50%;
  transform: translateX(-50%);
}

.header-nav__link:hover:after {
  width: 100%;
}

/*****************************HOME*************************/

.home {
  padding-top: 25px;
  min-width: 100%;
  min-height: calc(100vh - 70px);
  background-color: var(--main-color);
}

.home-cover {
  width: 65%;
  margin: 0 auto;
  height: 80vh;
  background-color: var(--main-light-color);
  position: relative;
}

.home-info {
  position: absolute;
  z-index: 10000;
  top: 40%;
  left: 12%;
}

.home-info__title {
  font-family: "Playfair Display", sans-serif;
  font-size: 54px;
  color: var(--text-color);
  font-weight: bold;
  margin-bottom: 10px;
  letter-spacing: 0.05em;
}

.home-info__subtitle {
  font-size: 32px;
  color: var(--text-color);
  margin-bottom: 35px;
}

.home-info__btn,
.home-info__btn:visited {
  text-decoration: none;
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  width: 165px;
  padding: 10px 0;
  display: block;
  background-color: var(--hl-color);
  color: #000;
  border-radius: 50px;
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.home-info__btn:hover {
  color: #000;
}

.home-info__btn::after {
  content: "";
  background: var(--text-color);
  position: absolute;
  z-index: -1;
  display: block;
  transition: all 0.35s;
}

.home-info__btn:hover::after {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  transition: all 0.35s;
}

.home-info__btn::after {
  left: 0;
  right: 0;
  top: -100%;
  bottom: 100%;
}

.home-carousel.swiper-container {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1000;
  width: 55%;
  height: 100%;
}

.home-carousel__slide {
  border-radius: 450px 0px 0px 450px;
  width: 100%;
  height: 100%;
}

.home-carousel__slide-first {
  background-image: url(/Portfolio/static/media/main.fba3dcd6.jpg);
  background-size: cover;
  background-position: right;
}

.home-carousel__slide-second {
  background-image: url(/Portfolio/static/media/main_2.f4390e4d.jpg);
  background-size: cover;
}
.home-carousel__slide-third {
  background-image: url(/Portfolio/static/media/main_3.1a536572.jpg);
  background-size: cover;
  background-position: center;
}
/*************************ABOUT*******************************/

.about {
  padding: 50px;
  min-width: 100%;
  min-height: calc(100vh - 70px);
  background-color: var(--main-color);
}

.about__title {
  text-align: center;
  text-transform: capitalize;
  margin-bottom: 20px;
  color: var(--hl-color);
}

.about h3 {
  font-size: 20px;
}

.about-top {
  width: 75%;
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
  align-items: flex-end;
  background-color: var(--main-light-color);
  padding: 0 50px 50px 0;
  border-radius: 250px 0 0 0;
}

.about svg {
  background: url("https://i.ytimg.com/vi/MlCODNJpJ84/maxresdefault.jpg");
  background-size: cover;
  width: 450px;
  height: auto;
  display: block;
  border-radius: 250px 0 0 0;
}

svg:hover {
  cursor: pointer;
}

text {
  font-size: 85px;
  transition: font-size 0.4s ease-out;
  font-weight: bold;
}

.about svg:hover text {
  transition: font-size 0.4s ease-in;
  font-size: 1700px;
}

.personal-info {
  display: flex;
  flex-direction: column;
}

.personal-info__title {
  margin-bottom: 30px;
  text-transform: uppercase;
  align-self: center;
}

.personal-info__item {
  margin-bottom: 10px;
  text-decoration: none;
  color: var(--text-color);
}

a.personal-info__item:hover {
  color: #febe27;
}

.personal-info__item b {
  color: var(--hl-color);
}

.about-middle,
.about-bottom {
  width: 75%;
  margin: 0 auto;
  padding: 50px 70px;
  display: flex;
  justify-content: space-between;
  background-color: var(--main-light-color);
}

.education__subtitle,
.education__item,
.experience__subtitle,
.experience__item {
  border-left: 2px solid var(--hl-color);
  padding-left: 20px;
  margin: 0;
}

.education__title,
.experience__title,
.coding-skills__title,
.language-skills__title {
  margin-bottom: 30px;
  text-transform: uppercase;
  text-align: center;
}

.education__subtitle,
.experience__subtitle {
  padding-bottom: 20px;
  color: var(--hl-color);
  position: relative;
  font-size: 20px;
}

.education__subtitle-second,
.experience__subtitle-second {
  padding-top: 50px;
}

.experience__item,
.education__item {
  padding-bottom: 10px;
}

progress {
  background-color: var(--text-color);
  width: 300px;
  display: block;
  margin-bottom: 20px;
}

progress::-webkit-progress-bar {
  background-color: var(--text-color);
}
progress::-webkit-progress-value {
  background-color: var(--hl-color);
}

/****************************PORTFOLIO**************************/

.portfolio {
  padding: 50px 100px;
  /* min-width: 100%; */
  min-height: calc(100vh - 70px);
  background-color: var(--main-color);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 30px;
  gap: 30px;
}

.card {
  width: 20vw;
  height: 20vw;
  transition: 1s;
  border-radius: 10px;
  position: relative;
}

.card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
  border-radius: 5px;
}

.portfolio:hover .card {
  transform: scale(0.9);
}

.card:hover {
  transform: scale(1.1) !important;
}

.card__info {
  display: none;
  width: 20vw;
  height: 20vw;
  background-color: rgba(241, 214, 214, 0.904);
  border-radius: 5px;
}

.card:hover .card__info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 50000;
}

.card__title {
  text-transform: uppercase;
  font-weight: bold;
  color: var(--main-light-color);
}

.card__text {
  color: var(--main-light-color);
  font-weight: bold;
}

.card__btn {
  text-align: center;
  display: inline-block;
  position: relative;
  text-decoration: none;
  color: #000;
  font-weight: bold;
  text-transform: capitalize;
  background-color: var(--hl-color);
  padding: 10px 0px;
  width: 150px;
  border-radius: 10px;
  overflow: hidden;
  transition: all 0.2s linear 0s;
  margin-top: 20px;
}

.card__btn:before {
  content: "→";
  font-size: 30px;
  position: absolute;
  color: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 0;
  top: 0;
  opacity: 0;
  height: 100%;
  width: 40px;
  transition: all 0.2s linear 0s;
}

.card__btn:hover {
  text-indent: -20px;
  color: #000;
}

.card__btn:hover:before {
  opacity: 1;
  text-indent: 0px;
}

/*************SAVE_THE_ALES******************/

.savetheales {
  padding: 20px;
  min-width: 100%;
  min-height: calc(100vh - 70px);
  background-color: var(--main-color);
}

.mySwiper2,
.mySwiper2 > .swiper-container {
  width: 60%;
  height: 55vh;
  margin: 0 auto 15px;
}

.mySwiper {
  width: 60%;
  height: 10vh;
  margin: 0 auto 15px;
}
.mySwiper:hover {
  cursor: pointer;
}

.slider-main__slide-one,
.slider-small__slide-one {
  background: url(/Portfolio/static/media/saveales-1.0259c8d3.png);
}

.slider-main__slide-two,
.slider-small__slide-two {
  background: url(/Portfolio/static/media/saveales-2.a1666e23.png);
}

.slider-main__slide-three,
.slider-small__slide-three {
  background: url(/Portfolio/static/media/saveales-3.63931da0.png);
}

.slider-main__slide-four,
.slider-small__slide-four {
  background: url(/Portfolio/static/media/saveales-4.40df0c88.png);
}

.slider-main__slide-five,
.slider-small__slide-five {
  background: url(/Portfolio/static/media/saveales-5.897eae80.png);
}

.slider-main__slide,
.slider-small__slide {
  background-size: cover;
}

.savetheales-text {
  text-align: center;
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 2em;
  width: 55%;
  margin: 0 auto;
}

.savetheales-text__link {
  text-decoration: none;
  transition: background-size 0.2s;
  color: var(--hl-color);
  font-weight: bold;
  position: relative;
}
.savetheales-text__link:hover {
  color: #000;
  padding: 5px 8px;
  background-color: var(--hl-color);
  border-radius: 5px;
}

/*************************CONTACT**********************/

.contact {
  padding: 50px 100px;
  min-width: 100%;
  min-height: calc(100vh - 70px);
  background-color: var(--main-color);
}

.contact__title {
  text-align: center;
  text-transform: capitalize;
  margin-bottom: 20px;
  color: var(--hl-color);
}

.contact__form {
  margin: 0 auto 25px;
  width: 40vw;
}

.form__btn {
  display: block;
  margin: 0 auto;
  padding: 7px 30px;
  color: #000;
  background-color: var(--hl-color);
  border: none;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 5px;
  border: 2px solid var(--hl-color);
}

.form__btn:hover {
  background-color: var(--main-color);
  color: var(--hl-color);
  border: 2px solid var(--hl-color);
}

.form-alert {
  color: var(--hl-color);
}

.contact__icons {
  justify-content: center;
  display: flex;
  grid-gap: 50px;
  gap: 50px;
}

.contact__icons svg:hover {
  fill: var(--hl-color);
}

.message-success {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 50px 0;
}

.message-success > h3 {
  margin-top: 20px;
}

@media (min-width: 1000px) and (max-width: 1200px) {
  /*****************************HOME*************************/

  .home-info__title {
    font-size: 48px;
  }

  .home-info__subtitle {
    font-size: 28px;
  }

  /*************************ABOUT*******************************/

  .about-top {
    width: 80%;
    padding: 0 45px 50px 0;
  }

  .about svg {
    width: 380px;
    height: 300px;
  }

  text {
    font-size: 75px;
  }

  .about-bottom,
  .about-middle {
    width: 80%;
    padding: 50px 45px;
  }
}

@media (min-width: 768px) and (max-width: 999px) {
  /*****************************HOME*************************/

  .home {
    padding-top: 15px;
  }

  .home-cover {
    width: 75%;
  }

  .home-info__title {
    font-size: 42px;
  }

  .home-info__subtitle {
    font-size: 24px;
  }

  .home-info__btn {
    width: 150px;
    padding: 5px;
  }

  /*************************ABOUT*******************************/
  .about {
    padding: 50px 30px;
  }

  .about-top,
  .about-middle,
  .about-bottom {
    width: 565px;
    padding: 0 0 50px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 50px;
    gap: 50px;
  }

  .about svg,
  .about rect {
    width: 565px;
    height: 250px;
  }

  text {
    font-size: 90px;
  }

  /****************************PORTFOLIO**************************/

  .card,
  .card__info {
    width: 30vw;
    height: 30vw;
  }

  .mySwiper2,
  .mySwiper2 > .swiper-container,
  .mySwiper {
    width: 70%;
  }

  .savetheales-text {
    width: 65%;
    font-size: 16px;
  }
}

@media (min-width: 500px) and (max-width: 767px) {
  /*****************************HOME*************************/

  .home {
    padding-top: 10px;
  }

  .home-cover {
    width: 80%;
  }

  .home-info {
    left: 10%;
  }

  .home-info__title {
    font-size: 38px;
  }

  .home-info__subtitle {
    font-size: 20px;
  }

  .home-info__btn {
    width: 130px;
    padding: 5px;
    font-size: 16px;
  }

  /*************************ABOUT*******************************/

  .about {
    padding: 50px 30px;
  }

  .about-top,
  .about-middle,
  .about-bottom {
    width: 400px;
    padding: 0 0 50px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 50px;
    gap: 50px;
  }

  .about svg,
  .about rect {
    width: 400px;
    height: 250px;
  }

  text {
    font-size: 80px;
  }
  /****************************PORTFOLIO**************************/

  .portfolio {
    padding: 50px 30px;
  }

  .card,
  .card__info {
    width: 40vw;
    height: 40vw;
  }

  .card__title {
    font-size: 20px;
  }

  .card__btn {
    width: 125px;
    padding: 5px;
  }

  .card__btn::before {
    font-size: 25px;
    width: 15px;
    right: 5px;
  }

  .mySwiper2,
  .mySwiper2 > .swiper-container,
  .mySwiper {
    width: 100%;
  }

  .slider-main__slide,
  .slider-small__slide {
    background-size: cover;
    background-position-x: 10%;
  }

  .savetheales-text {
    width: 90%;
    font-size: 16px;
  }

  .contact__icons {
    grid-gap: 20px;
    gap: 20px;
  }
}

@media (max-width: 499px) {
  .header {
    font-size: 14px;
  }

  /*****************************HOME*************************/

  .home {
    padding-top: 10px;
  }

  .home-cover {
    width: 90%;
  }

  .home-info {
    top: 65%;
  }

  .home-info__title {
    font-size: 28px;
  }

  .home-info__subtitle {
    font-size: 18px;
    margin-bottom: 20px;
  }

