    width: 115px;
    padding: 5px;
    font-size: 14px;
  }

  .home-carousel.swiper-container {
    width: 100%;
    height: 60%;
  }

  .home-carousel__slide {
    border-radius: 0px 0px 0px 450px;
    width: 100%;
    height: 100%;
  }

  /*************************ABOUT*******************************/

  .about {
    padding: 10px 0;
    font-size: 14px;
  }

  .about h3 {
    font-size: 14px;
  }

