    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
    padding: 15px;
  }

  .savetheales::after {
    content: "Save The Ales";
    color: var(--hl-color);
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
  }

  .mySwiper2,
  .mySwiper2 > .swiper-container {
    width: 100%;
    height: 50vh;
  }

  .slider-main__slide {
    background-size: contain;
    background-repeat: no-repeat;
    width: 100%;
  }

  .mySwiper {
    display: none;
  }

  .savetheales-text {
    width: 80%;
    font-size: 14px;
    margin: 0 auto;
  }

  .contact {
    padding: 20px 0;
  }

  .contact__form {
    width: 70vw;
    margin: 0 auto 25px;
  }

  .contact__icons {
    grid-gap: 20px;
    gap: 20px;
  }
}

